* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: linear-gradient(to right, #fc466b, #3f5efb);
}

.container {
  margin-top: 30px;
  padding-top: 40px;
  padding-bottom: 40px;
  height: 600px;
}
.sidebar_style {
  background-color: white;
  box-shadow: 0 0 10px #000;
  border-radius: 20px 0px 0px 0px;
}
.main_style {
  background-color: #ffffff;
  border-radius: 0px 0px 20px 0px;
  box-shadow: 0 0 10px #000;
  padding: 0px !important;
  margin-top: 30px;
}
.fixed__bottom{
  padding-bottom: 30px;
}
.body_part img{
    border-radius: 50%;
}
.container{
    text-align: center;
    padding: 0px !important;
}

.style_body{
    background: linear-gradient(to right, #f902c2, #3fa6ff);
    border-radius: 20px 0px 0px 0px;
    color: #ffffff;
    padding: 20px 0px;
    
}
.download a{
    color:#ffffff;
    background-color: rgb(0, 162, 255);
    padding: 5px;

}
.icon_part{
    padding: 10px 0px;
}

.Address_bar{
   padding: 20px 0px;
}
.btn {
    background: linear-gradient(to right, #fc00ff, #00dbde) !important;
    border: none;
    outline: none;
    box-shadow: 0 0 5px black;
}
.btn:focus{
   box-shadow: none;
}
.main_header{
   
    background: linear-gradient(to right, #f38bdd, #87bbe9);
